<template>
   <div class='roles'>
          <!-- <h3>用户列表组件面包屑导航</h3> -->
       <el-breadcrumb separator-class="el-icon-arrow-right">
  <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
  <el-breadcrumb-item>权限管理</el-breadcrumb-item>
  <el-breadcrumb-item>角色列表</el-breadcrumb-item>
</el-breadcrumb>
<el-card>
    <!-- 添加角色按钮区域 -->
    <el-row> 
        <el-col> 
            <el-button type="primary">添加角色</el-button>
        </el-col>
         </el-row>
         <!-- 角色列表区域 -->
         <el-table :data="rolelist" border stripe>  
             <!-- 展开列表 -->
              <el-table-column type="expand">
                  <template slot-scope="scope">
                      <el-row :class="['bdbottom', i1 === 0 ? 'bdtop': '','vcenter']" v-for="(item1, i1) in scope.row.children" :key="item1.id">
                          <!-- 渲染一级权限 -->
                          <el-col :span="5"> 
                             <el-tag closable @close="removeRightById(scope.row,item1.id)" >
                                {{item1.authName}}
                             </el-tag>
                             <i class="el-icon-caret-right"></i>
                          </el-col>
                          <!-- 渲染二级和三级权限 -->
                           <el-col :span="19"> 
                               <!-- 通过for 循环嵌套来渲染二级权限 -->
                                    <el-row :class="[i2 ===0 ? '':'bdtop','vcenter']" v-for="(item2,i2) in item1.children" :key="item2.id"> 

                                            <el-col :span="6">
                                                <el-tag type="success" closable @close="removeRightById(scope.row,item2.id)" >
                                                    {{item2.authName}}
                                                </el-tag>
                                                  <i class="el-icon-caret-right"></i>
                                            </el-col>
                                            <el-col :span="18">
                                                <el-tag type="warning" v-for="(item3,i3) in item2.children" :key="item3.id" closable @close="removeRightById(scope.row,item3.id)">
                                                                 {{item3.authName}}   
                                                     </el-tag>
                                            </el-col>
                                    </el-row>
                           </el-col>
                      </el-row>
                      <!-- pre 是显示结构 -->
                    <!-- <pre>
                         {{scope.row}} 
                    </pre> -->
                  </template>
              </el-table-column>
             <!-- 索引列表 -->
                <el-table-column type="index"></el-table-column>
                <el-table-column label="角色名称" prop="roleName"></el-table-column>
                <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
                 <el-table-column label="操作" width="300px" >
                     <template slot-scope="scope">
                        <el-button type="primary" icon="el-icon-edit" size="mini">编辑</el-button>
                        <el-button type="danger" icon="el-icon-delete" size="mini">删除</el-button>
                        <el-button type="warning" icon="el-icon-setting" size="mini" @click="showSetRightDialog(scope.row)">分配权限</el-button>
                     </template>
                 </el-table-column>

         </el-table>
</el-card>
<!-- 分配权限的对话框 -->
<el-dialog
  title="分配权限"
  :visible.sync="setRightDialogVisible"
  width="50%"
  @close="setRightDialogClosed"
  >
  <!-- 这是树形控件 -->
  <el-tree :data="rightslist" :props="treeProps" show-checkbox node-key="id"
   default-expand-all :default-checked-keys="defKeys" ref="treeRef">

  </el-tree>
  <span slot="footer" class="dialog-footer">
    <el-button @click="setRightDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="allotRights">确 定</el-button>
  </span>
</el-dialog>
   </div>
</template>

<script>
export default {
   data() {
   //这里存放数据
       return {
            rolelist:[], //这是存放角色列表的数据  
            setRightDialogVisible:false,   //这是控制权限对话框的显示和隐藏
            rightslist:[], // 这是所有的权限数据
            treeProps:{   //这是树形控件的属性绑定对象
                    label: 'authName',
                    children: 'children',
            },
            defKeys:[],  //这是默认绑定的节点数组
            roleId:'', //当前即将分配角色的权限id
       };
   },
   //import引入的组件需要注入到对象中才能使用
   components: {

   },
   //监听属性 类似于data概念
   computed: {

   },
   //监控data中的数据变化
   watch: {

   },
   //方法集合
   methods: {
      async  getRolesList(){
       let {data:res} = await this.$http.get("roles")
       if(res.meta.status !==200){
           return this.$message.error("获取角色列表失败")
       }
       this.rolelist =res.data
       console.log(this.roleList)
       },
       //点击删除，根据id删除用户权限
     async removeRightById(role,rightId){
           //显示弹框是否要删除提示
        let confirmResult =   await this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err=>err)
        if(confirmResult !== 'confirm'){
            return  this.$message.info("取消了删除")
        }
     let {data:res} = await   this.$http.delete(`roles/${role.id}/rights/${rightId}`)
     console.log(res)
     if(res.meta.status !==200){
         return this.$message.error("删除权限失败")
     }
       role.children =res.data 
       },
       //点击分配权限按钮显示出对话框，并设置
       async  showSetRightDialog(role){
           this.roleId =role.id
            // 获取所有权限的数据
          let {data:res} = await this.$http.get("rights/tree")
          console.log(res)
          if(res.meta.status !== 200){
              return this.message.error("获取权限数据失败")
          }
                //把获取到的权限数据保存到rightslist中
                this.rightslist =res.data
                console.log(this.rightslist)
                 this.getLeafKeys(role, this.defKeys) //用递归来获取三级节点的id
                this.setRightDialogVisible = true   //让对话框显示
       },
       //这是通过递归的形式，获取角色下所有三级权限的id，并保存到 defkeys数组中
       getLeafKeys(node,arr){
           // 判断如果当前node属性不包含children属性则是三级节点
            if(!node.children){
                return arr.push(node.id)
            }
            node.children.forEach(item => this.getLeafKeys(item,arr))
       },
       //监听分配权限对话框的关闭事件
       setRightDialogClosed(){
           this.defKeys=[]
       },
       //点击确定为角色分配权限
      async  allotRights(){
           let keys=[
                    ...this.$refs.treeRef.getCheckedKeys(),
                    ...this.$refs.treeRef.getHalfCheckedKeys()
           ]
           let idstr =keys.join(',')
           console.log(idstr)
         let {data:res} = await  this.$http.post(`roles/${this.roleId}/rights`,{rids:idstr})
         if(res.meta.status !==200){
             return this.$message.error("分配权限失败")
         }
         this.$message.success("分配权限成功")
         this.getRolesList()  //刷新角色列表
         this.setRightDialogVisible=false   //关闭权限对话框
       }
   },
   //生命周期 - 创建完成（可以访问当前this实例）
   created() {
       this.getRolesList()
   },
   //生命周期 - 挂载完成（可以访问DOM元素）
   mounted() {

   },
   beforeCreate() {}, //生命周期 - 创建之前
   beforeMount() {}, //生命周期 - 挂载之前
   beforeUpdate() {}, //生命周期 - 更新之前
   updated() {}, //生命周期 - 更新之后
   beforeDestroy() {}, //生命周期 - 销毁之前
   destroyed() {}, //生命周期 - 销毁完成
   activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style scoped lang="less">
.el-tag{
    margin:8px;
}
.bdtop{
border-top:1px solid #eee;
}
.bdbottom{
border-bottom: 1px solid #eee;
}
.vcenter{
    display: flex;
    align-items: center;
}
</style>