<template>
   <div class=''>
        <!-- <h3>商品管理组件面包屑导航</h3> -->
       <el-breadcrumb separator-class="el-icon-arrow-right">
  <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
  <el-breadcrumb-item>商品管理</el-breadcrumb-item>
  <el-breadcrumb-item>商品分类</el-breadcrumb-item>
</el-breadcrumb>
<!-- 卡片视图区域 -->
<el-card>
   <el-row> 
       <el-col> 
           <el-button type="primary" @click="showAddCateDialog">添加分类 </el-button>
       </el-col>
   </el-row>
   <!-- 表格区域 -->
            <tree-table  class="treeTable" :data="catelist" :columns="columns" :selection-type="false" 
            :expand-type="false" show-index index-text="#" border :show-row-hover="false"> 
                <!-- 是否有效哦 -->
            <template slot="isok" slot-scope="scope">
                <i class="el-icon-success" v-if="scope.row.cat_deleted === false" style="color:lightgreen;"></i>
                <i class="el-icon-error" v-else style="color:red;"></i>
            </template>
            <!-- 排序 -->
                <template slot="order" slot-scope="scope">
                   <el-tag size="mini" v-if="scope.row.cat_level===0">一级</el-tag>
                    <el-tag type="success" size="mini" v-else-if="scope.row.cat_level===1">二级</el-tag>
                     <el-tag type="warning" size="mini" v-else>三级</el-tag>
                </template>
                <!-- 操作 -->
                <template slot="opt" slot-scope="scope">
                    <el-button type="primary" icon="el-icon-edit" size="mini">编辑</el-button>
                    <el-button type="danger" icon="el-icon-delete" size="mini">删除</el-button>
                </template>
             </tree-table>  
   <!-- 分页区域 -->
   <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="querInfo.pagenum"
      :page-sizes="[3, 5, 10, 15]"
      :page-size="querInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
</el-card>
        <!-- 添加分类的对话框按钮 -->
        <el-dialog
  title="添加分类"
  :visible.sync="addCateDialogVisible"
  width="50%"
  @close="addCateDialogClosed"
  >
  <!-- 添加分类的表单 -->
 <el-form :model="addCateForm" :rules="addCateFormRules" ref="addCateFormRef" label-width="100px" >
  <el-form-item label="分类名称：" prop="cat_name">
    <el-input v-model="addCateForm.cat_name"></el-input>
  </el-form-item>
  <el-form-item label="父级分类：" >
      <!-- options用来指定数据源   props  cascaderProps  用来指定配置对象-->
   <el-cascader
    v-model="selectedKeys"
    :options=" parentCateList"
    :props="cascaderProps"
     expand-trigger="hover" 
    @change="parentCateChanged"
    clearable change-on-select
    >
    </el-cascader>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="addCateDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="addCate">确 定</el-button>
  </span>
</el-dialog>
   </div>
</template>

<script>
export default {
   data() {
   //这里存放数据
       return {
            catelist:[], //商品分类的数据列表，默认为空
            querInfo:{
                type:3,  //类型
                pagenum:1, //当前页码值
                pagesize:5 //每页的多少条数据
            },
            total:0,  //总数据条数
            columns:[{    //为table指定列的定义
                label:'分类名称',
                prop:'cat_name'
            },
            {
                label:'是否有效',
                type:"template", //表示当前定义为模板列
                template:'isok',  //表示当前这一列使用的模板名称

            },
             {
                label:'排序',
                type:"template", //表示当前定义为模板列
                template:'order',  //表示当前这一列使用的模板名称

            },
            {
                label:'操作',
                type:"template", //表示当前定义为模板列
                template:'opt',  //表示当前这一列使用的模板名称

            }
            ], 
            addCateDialogVisible:false, //控制添加确定按钮的显示和隐藏
            addCateForm:{//添加分类的表单数据对象
                    cat_name:'',  //添加的分类的名称
                    cat_pid:0,  //父级分类的id
                    cat_level:0,  //分类的等级，默认要添加的是一级分支
            },  
            addCateFormRules:{  //添加分裂表单的验证规则对象
                    cat_name:[
                         { required: true, message: '请输入分类的名称', trigger: 'blur' }, 

                    ]
            } ,
            parentCateList:[],  //父级分类的列表
            cascaderProps:{   //指定级联选择器的配置对象
                value:'cat_id',
                label:'cat_name',
                children:'children',
            } ,
            selectedKeys:[]  //选中的父级分类的id数组
            
       };
   },
   //import引入的组件需要注入到对象中才能使用
   components: {

   },
   //监听属性 类似于data概念
   computed: {

   },
   //监控data中的数据变化
   watch: {

   },
   //方法集合
   methods: {
       //获取商品分类的数据
    async    getCateList(){
          let {data:res} =  await this.$http.get("categories",{params: this.querInfo})
                if(res.meta.status !== 200){
                    return this.$message.error("获取商品分类失败")
                }
                    this.catelist=res.data.result  //把获取到的商品分类列表存到catelist
                    console.log(res.data)
                    this.total=res.data.total  //为总数据条数赋值
                },
                //监听pagesize改变的事件
                handleSizeChange(newSize){
                        this.querInfo.pagesize=newSize
                        this.getCateList()
                },
                //监听pagenum的改变
                handleCurrentChange(newPage){
                        this.querInfo.pagenum=newPage
                         this.getCateList()
                },
                //点击添加按钮触发的事件
                showAddCateDialog(){
                       this.getParentCateList()  //先获取数据分类的数据列表
                        this.addCateDialogVisible=true
                     
                },
                //获取父级分类的数据列表
           async   getParentCateList(){
                 let {data:res} = await  this.$http.get('categories',{params:{
                        type:2,
                    }})
                    if(res.meta.status!==200){
                        return this.$message.error("获取分类数据失败")
                    }
                  
                    this. parentCateList =res.data
                      console.log(this. parentCateList)
                },
                //选择项发生变化触发这个函数
                parentCateChanged(){
                         console.log(this.selectedKeys)   
                         //如果selectedKeys 数组中的length 大于0，证明选中父级分类，
                         //反之，没有选中任何父级分类
                         if(this.selectedKeys.length>0){
                             //父级分类id
                           this.addCateForm.cat_pid = this.selectedKeys[this.selectedKeys.length-1]
                           //当前分类的等级赋值
                           this.addCateForm.cat_level=this.selectedKeys.length
                           return 
                         }else{
                                //父级分类id
                           this.addCateForm.cat_pid = 0
                           //当前分类的等级赋值
                           this.addCateForm.cat_level=0
                         }
                },
                //点击确定按钮添加新的分类
                addCate(){
                   this.$refs.addCateFormRef.validate( async valid =>{
                       if(!valid) return 
                     let {data:res} = await  this.$http.post("categories", this.addCateForm)
                     if(res.meta.status !==201){
                         return this.$message.error("添加分类失败")
                     }
                     this.$message.success("添加分类成功")
                     this.getCateList()
                     this.addCateDialogVisible=false
                   })

                },
                //监听对话框的关闭事件，重置表单数据
                addCateDialogClosed(){
                            this.$refs.addCateFormRef.resetFields()
                            this.selectedKeys=[]
                            this.addCateForm.cat_level=0
                             this.addCateForm.cat_pid=0
                }
               
   },
   //生命周期 - 创建完成（可以访问当前this实例）
   created() {
        this.getCateList()  
       
   },
   //生命周期 - 挂载完成（可以访问DOM元素）
   mounted() {

   },
   beforeCreate() {}, //生命周期 - 创建之前
   beforeMount() {}, //生命周期 - 挂载之前
   beforeUpdate() {}, //生命周期 - 更新之前
   updated() {}, //生命周期 - 更新之后
   beforeDestroy() {}, //生命周期 - 销毁之前
   destroyed() {}, //生命周期 - 销毁完成
   activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style scoped lang="less">
.treeTable{
    margin-top:15px;
}
.el-cascader{
    width:100%;
}
</style>