<template>
   <div class=''>
         <!-- 面包屑导航 -->
       <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>商品管理</el-breadcrumb-item>
            <el-breadcrumb-item>参数列表</el-breadcrumb-item>
            </el-breadcrumb>
           <el-card>
               <!-- //这是头部的警告区域 -->
              <el-alert
                title="注意：只允许为第三级分类设置相关参数！"
                type="warning"
                show-icon
                :closable="false"
                >
            </el-alert>
            <!-- 选择商品分类区域 -->
            <el-row class="cat_opt"> 
                <el-col> 
                    <span>选择商品分类:</span>
                    <!-- 选择商品分类的级联选择框 -->
                    <el-cascader
                    v-model="selectedCateKeys"
                    :options="catelist"
                    :props="cateProps"
                    expand-trigger="hover" 
                    @change="handleChange"></el-cascader>
                </el-col>
            </el-row>
        <!-- tab标签页的区域 -->
         <el-tabs v-model="activeName" @tab-click="handleTabClick">
             <!-- 添加动态参数的面板 -->
    <el-tab-pane label="动态参数" name="many">
                <!-- 添加参数的按钮 -->
            <el-button  type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible=true">添加参数</el-button>
            <!-- 动态参数表格 -->
            <el-table :data=" manyTableData" border stripe>
                <!-- 展开行 -->
                <el-table-column type="expand">
                    <template slot-scope="scope">
                        <!-- 循环渲染tag标签 -->
                       <el-tag v-for="(item,i) in scope.row.attr_vals" :key="i"
                        closable @close="handleClose(i,scope.row)"> 
                            {{item}}
                       </el-tag>
                       <!-- 输入文本框 -->
                       <el-input
                        class="input-new-tag"
                        v-if=" scope.row.inputVisible"
                        v-model=" scope.row.inputValue"
                        ref="saveTagInput"
                        size="small"
                        @keyup.enter.native="handleInputConfirm(scope.row)"
                        @blur="handleInputConfirm(scope.row)"
                        >
                        </el-input>
                        <!-- 添加的按钮 -->
                        <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
                    </template>
                </el-table-column>
                    <!-- 索引列 -->
                    <el-table-column type="index"></el-table-column>
                    <el-table-column  label="参数名称" prop="attr_name"></el-table-column>
                        <el-table-column  label="操作" >
                            <template slot-scope="scope" >
                               <el-button type="primary" icon="el-icon-edit" size="mini" 
                                @click="showEditDialog(scope.row.attr_id)" >编辑</el-button>
                               <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeParams(scope.row.attr_id)">删除</el-button>
                            </template>
                        </el-table-column>
            </el-table>
    </el-tab-pane>
            <!-- 添加静态属性的面板 -->
    <el-tab-pane label="静态属性" name="only">
        <!-- 添加属性的按钮 -->
        <el-button  type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible=true">添加属性</el-button>
        <!-- 静态属性表格 -->
         <el-table :data="onlyTableData" border stripe>
                <!-- 展开行 -->
                 <el-table-column type="expand">
                    <template slot-scope="scope">
                        <!-- 循环渲染tag标签 -->
                       <el-tag v-for="(item,i) in scope.row.attr_vals" :key="i"
                        closable @close="handleClose(i,scope.row)"> 
                            {{item}}
                       </el-tag>
                       <!-- 输入文本框 -->
                       <el-input
                        class="input-new-tag"
                        v-if=" scope.row.inputVisible"
                        v-model=" scope.row.inputValue"
                        ref="saveTagInput"
                        size="small"
                        @keyup.enter.native="handleInputConfirm(scope.row)"
                        @blur="handleInputConfirm(scope.row)"
                        >
                        </el-input>
                        <!-- 添加的按钮 -->
                        <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
                    </template>
                </el-table-column>
                    <!-- 索引列 -->
                    <el-table-column type="index"></el-table-column>
                    <el-table-column  label="属性名称" prop="attr_name"></el-table-column>
                        <el-table-column  label="操作" >
                            <template slot-scope="scope" >
                               <el-button type="primary" icon="el-icon-edit" size="mini"
                                @click="showEditDialog(scope.row.attr_id)">编辑</el-button>
                               <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeParams(scope.row.attr_id)">删除</el-button>
                            </template>
                        </el-table-column>
            </el-table>
    </el-tab-pane>
  </el-tabs>
           </el-card>
            <!-- 这是添加参数的对话框 -->
           <el-dialog
  :title="'添加'+titleText"
  :visible.sync="addDialogVisible"
  width="50%"
    @close="addDialogClosed"
  >
  <!-- 添加参数的对话框 -->
 <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px" >
  <el-form-item :label="titleText" prop="attr_name">
    <el-input v-model="addForm.attr_name"></el-input>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="addDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="addParams">确 定</el-button>
  </span>
</el-dialog>
        <!-- 修改参数的对话框 -->
        <el-dialog
  :title="'修改'+titleText"
  :visible.sync="editDialogVisible"
  width="50%"
    @close="editDialogClosed"
  >
  <!-- 添加参数的对话框 -->
 <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px" >
  <el-form-item :label="titleText" prop="attr_name">
    <el-input v-model="editForm.attr_name"></el-input>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="editParams">确 定</el-button>
  </span>
</el-dialog>
   </div>
</template>

<script>
export default {
   data() {
   //这里存放数据
       return {
            catelist:[], //商品分类列表
            cateProps:{  //这是级联选择框的配置对象
                  value:'cat_id',
                label:'cat_name',
                children:'children',
            },
            selectedCateKeys:[], //这是级联选择框双向绑定到的数组
            activeName:'many', //被激活的页签名称
            manyTableData:[],  //many动态参数的数据
            onlyTableData:[],      // only静态属性的数据
            addDialogVisible:false,  // 这是控制添加对话框的显示和隐藏
            addForm:{ // 添加参数的表单数据对象
                attr_name:''
            }, 
            addFormRules:{  //这是添加表单的验证规则
                    attr_name:[
                         { required: true, message: '请输入参数的名称', trigger: 'blur' }, 
                    ]
            },
            editDialogVisible:false, //控制修改对话框的显示和隐藏
            editForm:{ // 修改的的表单数据对象

            },
            editFormRules:{ //这是修改表单的验证规则
                 attr_name:[
                         { required: true, message: '请输入参数的名称', trigger: 'blur' }, 
                    ]
            },
           
       };
   },
   //import引入的组件需要注入到对象中才能使用
   components: {

   },
   //监听属性 类似于data概念
   computed: {
       //如果按钮需要被警用，则返回true，否则返回fasle
        isBtnDisabled(){
                if(this.selectedCateKeys.length !==3){
                    return true
                }
                return false
        },
        //当前选中的三级分类的id
        cateId(){
                if(this.selectedCateKeys.length ===3){
                    return this.selectedCateKeys[2]
                }
                return null 
        },
        //动态计算标题的名字
        titleText(){
                if(this.activeName === "many"){
                    return '动态参数'
                }
                return '静态属性'
        }
   },
   //监控data中的数据变化
   watch: {

   },
   //方法集合
   methods: {
     async  getCateList() {
          let {data:res} = await  this.$http.get("categories")
          if(res.meta.status !==200){
              return this.$message.error("请求商品数据失败")
          }
        this.catelist =res.data
        console.log(this.catelist)
        },
        //级联选择框选中项变化，会触发这个函数
       handleChange(){
             this.getParamsDate()

        },
        //tab页签，点击事件的处理函数
        handleTabClick(){
                console.log(this.activeName)
                 this.getParamsDate()
        },
        //获取参数的列表数据
      async  getParamsDate(){
                //证明选中的不是三级分类
               if(this.selectedCateKeys.length !== 3){
                   this.selectedCateKeys=[]
                   this.manyTableData=[]
                   this.onlyTableData=[]
                    return 
               }
               //证明选中的是三级分类
               console.log(this.selectedCateKeys)
               //根据所选的分类id和当前所处的面板，来获取对应的参数
           let {data:res} = await   this.$http.get(`categories/${this.cateId}/attributes`,{
                   params:{sel:this.activeName}
               })
               if(res.meta.status !==200){
                   return this.$message.error("请求数据失败")
               }
              
               res.data.forEach(item =>{
                 item.attr_vals = item.attr_vals? item.attr_vals.split(' '):[]
                 //控制文本框的显示与隐藏
                 item.inputVisible =false
                 //文本框中输入的值
                 item.inputValue=""
               })
                console.log(res.data)
               //判断 activename属于哪个表格 
               if(this.activeName === "many"){
                   this.manyTableData= res.data
               }else{
                   this.onlyTableData =res.data
               }
        },
        //监听添加对话框的关闭事件
        addDialogClosed(){
                this.$refs.addFormRef.resetFields()
        },
        //点击按钮添加参数
        addParams(){
                this.$refs.addFormRef.validate( async valid =>{
                    if(!valid) return 
                 let {data:res} = await  this.$http.post(`categories/${this.cateId}/attributes`,{
                        attr_name:this.addForm.attr_name,
                        attr_sel:this.activeName
                    })
                    if(res.meta.status !==201){
                        return this.message.error("添加参数失败")
                    }
                    this.$message.success("添加参数成功")
                    this.addDialogVisible = false
                    this.getParamsDate()
                })
        },
        //点击按钮展示修改的对话框
     async   showEditDialog(attr_id){
            //查询当前参数的信息
                    let{data:res} = await   this.$http.get(`categories/${this.cateId}/attributes/${attr_id}`,{
                            params:{
                                attr_sel:this.activeName
                            }
                        })
                        if(res.meta.status !==200){
                            return this.message.error("获取参数失败")
                        }
                        this.editForm =res.data
                        this.editDialogVisible= true
        },
        editDialogClosed(){  //重置修改的表单
            this.$refs.editFormRef.resetFields()
        },
        //点击按钮修改参数信息
        editParams(){
                    this.$refs.editFormRef.validate( async valid =>{
                         if(!valid) return 
                      let {data:res} = await  this.$http.put(`categories/${this.cateId}/attributes/${this.editForm.attr_id}`,{
                            attr_name:this.editForm.attr_name,
                        attr_sel:this.activeName
                      })
                       if(res.meta.status !==200){
                        return this.message.error("修改参数失败")
                    }
                    this.$message.success("修改参数成功")
                    this.editDialogVisible = false
                    this.getParamsDate()
                    })
        },
        //根据id删除对应的参数项
      async  removeParams(attr_id){
           let confirmResult =  await      this.$confirm('此操作将永久删除该参数, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err=>err)
        if(confirmResult!== 'confirm'){
            return this.$message.info('已取消删除')
        }
            //删除的业务逻辑
          let {data:res} = await   this.$http.delete(`categories/${this.cateId}/attributes/${attr_id}`)
            if(res.meta.status !==200){
                        return this.message.error("删除参数失败")
                    }
                    this.$message.success("删除参数成功")
                     this.getParamsDate()
        },
        //文本框失去焦点，或者enter都会触发
      async  handleInputConfirm(row){
                    if(row.inputValue.trim().length === 0){
                        row.inputValue=''
                          row.inputVisible=false
                          return
                    }
                //如果没有return ，需要后续处理
                row.attr_vals.push(row.inputValue.trim())
                row.inputValue=""
                 row.inputVisible=false
                 //需要发起请求保存这次操作
            this.saveAttrVals(row)
        },
        //将对attr——vals的操作保存到数据库
       async saveAttrVals(row){
                  let{data:res} = await  this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`,{
                   attr_name:row.attr_name,
                        attr_sel:row.attr_sel,
                        attr_vals:row.attr_vals.join(' ')
             })
                if(res.meta.status !==200){
                        return this.message.error("修改参数失败")
                    }
                     this.$message.success("修改参数成功")
        },
        //点击按钮，展示文本输入框
        showInput(row){
               row.inputVisible =true
               //让文本框自动获得焦点
               //$nextTick 的作用就是当页面上元素被重新渲染之后才会执行的回调函数中的代码
               this.$nextTick(_ => {
          this.$refs.saveTagInput.$refs.input.focus();
        });
        },
            //删除对应的参数可选项
        handleClose(i,row){
                row.attr_vals.splice(i,1)
                this.saveAttrVals(row)
        }
        
        

   },
   //生命周期 - 创建完成（可以访问当前this实例）
   created() {
        this.getCateList() 
   },
   //生命周期 - 挂载完成（可以访问DOM元素）
   mounted() {

   },
   beforeCreate() {}, //生命周期 - 创建之前
   beforeMount() {}, //生命周期 - 挂载之前
   beforeUpdate() {}, //生命周期 - 更新之前
   updated() {}, //生命周期 - 更新之后
   beforeDestroy() {}, //生命周期 - 销毁之前
   destroyed() {}, //生命周期 - 销毁完成
   activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style scoped lang="less">
.cat_opt{
    margin:15px 0;
}
.el-tag{
    margin:10px;
}
.input-new-tag{
    width:120px;
}
</style>